import React from 'react';

import Editor from './Editor';
import Previewer from './Previewer';

class App extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			editorText: `
# Welcome to my Markdown Previewer
# With a Hash symbol, you can create an h1 element
## with a double hash symbol, you can create an h2 element
### With the triple hash symbol, you can create an h3 element
You can write one line code in with single backtick ex. \`<div></div>\`
\`\`\`
// this is multi-line code

function example(a, b){
	return a + b;
}
\`\`\`

You can make text **BOLD**!

Or _Italic_

or both **_woohoo_**!

some ~~Crossing stuff too~~

you can add [links](https://www.freecodecamp.com), and
> Block Quotes!

If you want to create tables, go ahead:

Wild Header | Crazy Header | Another Header?
------------ | ------------- | -------------
Your content can | be here, and it | can be here...
And here. | Okay. | I think we get it

And what about lists...
- list one.
  - Some are bulleted.
     - With different indentation levels.
        - That looks like this.

1. And there are numbered lists too.
1. Use just 1s if you want!
1. And last but not least, let's not forget embedded images:

![React Logo w/ Text](https://google.com)`
	}
	this.handleChange = this.handleChange.bind(this);
}

	handleChange(event){
		this.setState({
			editorText: event.target.value
		})
	}

	render(){
		return (
			<div id='container'>
				<Editor 
					editorText={this.state.editorText} 
					handleChange={this.handleChange}/>
				<Previewer 
					editorText={this.state.editorText}/>
			</div>
		)
	}
}

export default App;
